.footer-case {
    position: relative;
    z-index: 1;
    //
    &--green {
        background-color: #00aaa0;
    }
    // END

    &__container {
        max-width: 1880px;
        padding: 0 3.13%;
        margin: 0 auto;
        @media (min-width: 1920px) {
            max-width: 100%;
            width: 97.927%;
        }
    }
    &__label {
        @include adaptiv-font-fullScrean(20, 16);
        font-weight: 400;
        line-height: 180%; /* 36px */
        color: #fff;
        padding: 120px 0px 100px 80px;
        & a {
            text-decoration: underline;
            font-size: inherit;
            font-weight: 300;
            transition: all 0.3s;
            @media (hover: hover) {
                &:hover {
                    opacity: 0.7;
                }
            }
        }
    }

    &--hidden {
        display: none;
    }
}
// TOP
.footer-case {
    &__top {
        display: flex;
        justify-content: space-between;
        row-gap: 32px;
        column-gap: 3.4%;
        padding-bottom: 220px;
        padding-top: 48px;
        border-top: 1px solid rgba(255, 255, 255, 0.16);
    }
    &__top-columns {
        flex: 1 1 16.6666%;
    }
    &__top-items {
        display: flex;
        flex-direction: column;
        row-gap: 32px;
    }
    &__top-item {
        display: flex;
        flex-direction: column;
        row-gap: 6px;
    }
    &__top-title {
        line-height: 180%;
        letter-spacing: -0.4px;
        color: rgba(255, 255, 255, 0.6);
        @include adaptiv-font-fullScrean(20, 12);
        @media (max-width: 1920px) {
            font-size: 20px !important;
        }
    }
    &__top-images {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
    }
    &__top-img {
        line-height: 0;
        width: 48px;
        height: 48px;
        & svg {
            width: 100%;
            height: 100%;
            border-radius: 150px;
            border: 1px solid rgba(255, 255, 255, 0.16);
        }
        @media (min-width: 1920px) {
            width: 19.7%;
            height: 19.7%;
        }
    }
    &__top-list {
        padding: 0;
        color: #fff;
        list-style: none;
        & li {
            @include adaptiv-font-fullScrean(20, 12);
            line-height: 162%; /* 32.4px */
            letter-spacing: -0.4px;
            @media (max-width: 1920px) {
                font-size: 20px !important;
            }
        }
    }
}
// Slide
.footer-case {
    &__slide {
        padding-bottom: 100px;
        @media (min-width: 1920px) {
            padding-bottom: 5.7%;
        }
    }
    &__slide-container {
        position: relative;
        z-index: 2;
        width: 100%;
        height: 100%;
        max-height: 877px;
        @media (min-width: 1920px) {
            max-height: 100%;
        }
        & .swiper-slide {
            & img {
                width: 100%;
                height: 100%;
                display: flex;
            }
        }
        & .swiper-button-next,
        .swiper-button-prev {
            width: auto;
            height: 72px;
            padding: 0 32px;
            background-color: #242b2f;
            border-radius: 64px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #fff;
            @include adaptiv-font-fullScrean(20, 10.608);
            font-weight: 700;
            line-height: 120%;
            column-gap: 40px;
            transition: opacity 0.3s;
            z-index: 2222;
            top: 71.5%;
            text-wrap: nowrap;
            @media (hover: hover) {
                &:hover {
                    opacity: 0.7;
                }
            }
            &:after {
                display: none;
            }
            & svg {
                width: 17.7%;
                height: 22.3%;
            }
        }
        & .swiper-button-next {
            right: 15.3%;
        }
        & .swiper-button-prev {
            left: 15.3%;
            & svg {
                rotate: 180deg;
            }
        }
    }
}
// Bottom
.footer-case {
    &__bottom {
        padding-bottom: 100px;
    }
    &__bottom-body {
        max-width: 1240px;
        margin: 0 auto;
        padding: 0px 5.7%;
        text-align: center;
        color: #fff;
        @media (min-width: 1920px) {
            max-width: 100%;
            width: 70.45%;
        }
    }
    &__bottom-check {
        display: flex;
        align-items: center;
        justify-content: center;
        &:not(:last-child) {
            margin-bottom: 36px;
        }
        & svg {
            width: 100px;
            height: 100px;
        }
        @media (min-width: 1920px) {
            &:not(:last-child) {
                margin-bottom: 3.47%;
            }
            & svg {
                width: 9.62%;
                height: 9.62%;
            }
        }
    }
    &__bottom-title {
        font-variant-numeric: lining-nums proportional-nums;
        @include adaptiv-font-fullScrean(64, 24);
        font-weight: 700;
        line-height: 106%; /* 67.84px */
        &:not(:last-child) {
            margin-bottom: 24px;
        }
        @media (min-width: 1920px) {
            &:not(:last-child) {
                margin-bottom: 2.3%;
            }
        }
    }
    &__bottom-text {
        @include adaptiv-font-fullScrean(20, 12);
        font-weight: 400;
        line-height: 180%; /* 36px */
        &:not(:last-child) {
            margin-bottom: 56px;
        }
        @media (min-width: 1920px) {
            &:not(:last-child) {
                margin-bottom: 5.4%;
            }
        }
    }
    &__bottom-items {
        display: flex;
        column-gap: 40px;
        justify-content: center;
    }
    &__bottom-item {
        display: flex;
        align-items: center;
        transition: all 0.3s;
        @include adaptiv-font-fullScrean(16, 11);
        @media (hover: hover) {
            &:hover {
                opacity: 0.7;
            }
        }
        & svg {
            width: 24px;
            height: 24px;
            margin-right: 8px;
        }
    }
}
@media (max-width: $desktop) {
    // Slide
    .footer-case {
        &__slide-container {
            & .swiper-button-next,
            .swiper-button-prev {
                height: 62px;
                padding: 0 24px;
                border-radius: 32px;
                column-gap: 20px;
            }
            & .swiper-button-next {
                right: 12.3%;
            }
            & .swiper-button-prev {
                left: 12.3%;
            }
        }
    }
    // TOP
    .footer-case {
        &__top {
            flex-wrap: wrap;
        }
    }
}
@media (max-width: 768px) {
    .footer-case {
        &__container {
            padding: 0 32px;
        }
        &__label {
            padding: 80px 0px;
            font-size: 16px;
        }
    }
    //TOP
    .footer-case {
        &__top {
            flex-direction: column;
            padding: 48px 52px 120px;
        }
        &__top-item {
            flex-direction: row;
            justify-content: space-between;

            column-gap: 20px;
        }
        &__top-title {
            flex: 1 1 50%;
        }
        &__top-list {
            flex: 1 1 50%;
        }
        &__top-images {
            flex: 1 1 50%;
        }
    }
    // Slide
    .footer-case {
        &__slide {
            padding-bottom: 80px;
        }
        &__slide-container {
            & .swiper-button-next,
            .swiper-button-prev {
                height: 51px;
                padding: 0 20px;
                border-radius: 24px;
                top: 73.5%;
                @include adaptiv-font-fullScrean(24, 10.608);
                & svg {
                    width: 12.7%;
                }
            }
            & .swiper-button-next {
                right: 8.3%;
            }
            & .swiper-button-prev {
                left: 8.3%;
            }
        }
    }
    // BOTTOM
    .footer-case {
        &__bottom {
            padding-bottom: 80px;
        }
        &__bottom-body {
            padding: 0px 14.21%;
        }
        &__bottom-check {
            & svg {
                max-width: 100px;
                max-height: 100px;
                width: 19.85%;
                height: 19.85%;
            }
        }
        &__bottom-title {
            &:not(:last-child) {
                margin-bottom: 12px;
            }
        }
        &__bottom-text {
            @include adaptiv-font-fullScrean(22, 12);
            &:not(:last-child) {
                margin-bottom: 36px;
            }
        }
        &__bottom-item {
            @include adaptiv-font-fullScrean(18.5, 11);
        }
    }
}
@media (max-width: 650px) {
    // Slide
    .footer-case {
        &__slide-container {
            & .swiper-button-next,
            .swiper-button-prev {
                height: 40px;
                padding: 0 15px;
                border-radius: 20px;
                top: 73.5%;
                column-gap: 10px;
            }
            & .swiper-button-next {
                right: 10px;
            }
            & .swiper-button-prev {
                left: 7px;
            }
        }
    }
    // BOTTOM
    .footer-case {
        &__bottom-body {
            padding: 0px;
        }
    }
}
@media (max-width: $mobile) {
    .footer-case {
        &__top {
            flex-direction: column;
            padding: 24px 0 64px;
        }
    }
}
@media (max-width: $mobile-sm) {
    .footer-case {
        &__label {
            padding: 48px 0px;
        }
    }
    // TOP
    .footer-case {
        &__top {
            row-gap: 20px;
        }
        &__top-item {
            flex-wrap: wrap;
            row-gap: 4px;
        }
        &__top-title {
            font-size: 16px !important;
        }
        &__top-list li {
            font-size: 16px !important;
        }
    }
    // Slide
    .footer-case {
        &__slide {
            margin: 0px -28px;
            padding-bottom: 64px;
        }
        &__slide-container {
            & .swiper-button-next,
            .swiper-button-prev {
                height: 27px;
                padding: 0 10.61px;
                border-radius: 14.144px;
                top: 79.5%;
                column-gap: 7px;
            }
        }
    }
    // BOTTOM
    .footer-case {
        &__bottom {
            padding-bottom: 64px;
            margin: 0px -14.5px;
        }
        &__bottom-check {
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }
        &__bottom-title {
            &:not(:last-child) {
                margin-bottom: 8px;
            }
        }
        &__bottom-text {
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }
        &__bottom-items {
            column-gap: 10px;
            justify-content: space-between;
        }
        &__bottom-item {
            & svg {
                display: none;
            }
        }
    }
}
@media (max-width: 320px) {
    .footer-case {
        &__bottom-check {
            & svg {
                width: 56px;
                height: 56px;
            }
        }
    }
}
